import {lazy, Suspense} from 'react';
import { Route, Switch, useRouteMatch } from 'react-router-dom';
import Loading from '../../components/Loading'

const Login = lazy(() => import('./login'));
const Register = lazy(()=> import('./register'));
const ForgotPassword = lazy(()=> import('./forgot'));
const VerifyCode = lazy(()=> import('./verifycode'));
const Page404 = lazy(() => import('../404'));

function Auth(){
    
    const match = useRouteMatch();

    return(
        <div className="w-full h-screen bg-white dark:bg-black dark:text-white">
            <Suspense fallback={<Loading type="global"/>}>
                <Switch>
                    <Route exact path={`${match.path}/login`}>
                        <Login />
                    </Route>
                    <Route exact path={`${match.path}/register`}>
                        <Register />
                    </Route>
                    <Route exact path={`${match.path}/verifycode`}>
                        <VerifyCode />
                    </Route>
                    <Route exact path={`${match.path}/forgot`}>
                        <ForgotPassword />
                    </Route>
                    <Route path="*" component={Page404} />
                </Switch>
            </Suspense>
        </div>
        
    )
}

export default Auth;